/* ******************************************************************************************************************** */
/* ON OFF SWITCH */
.onoffswitch {
  position: relative;
  /* width: 72px; */
  width: 64px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.onoffswitch-checkbox {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

.onoffswitch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  /* border: 2px solid #999999; */
  border-radius: 10px;
  background-color: #deb01a;
}

.onoffswitch-inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  transition: margin 0.3s ease-in 0s;
}

.onoffswitch-inner:before,
.onoffswitch-inner:after {
  display: block;
  float: left;
  width: 50%;
  /* height: 30px; */
  height: 25px;
  padding: 0;
  line-height: 26px;
  font-size: 18px;
  color: white;
  font-family: Trebuchet, Arial, sans-serif;
  font-weight: bold;
  box-sizing: border-box;
}

.onoffswitch-inner:before {
  content: "On";
  padding-left: 7px;
  background-color: #deb01a;
  color: #ffffff;
  position: relative;
  left: -34px;
}

.onoffswitch-inner:after {
  content: "off";
  padding-right: 8px;
  background-color: #485e88;
  color: #ffffff;
  text-align: right;
}

.onoffswitch-switch {
  display: block;
  width: 21px;
  height: 21px;
  margin: 2px;
  margin-right: 7px;
  background: #ffffff;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 33px;
  /* border: 2px solid #999999; */
  border-radius: 15px;
  transition: all 0.3s ease-in 0s;
}

.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
  margin-left: 0;
}

.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
  right: 0px;
}

/* switch two */

.onoffswitch-label2 {
  background-color: #099c3a;
}

.onoffswitch-inner2:before {
  background-color: #099c3a;
}

/* switch three */

.onoffswitch-label3 {
  background-color: #009afe;
}

.onoffswitch-inner3:before {
  background-color: #009afe;
}

/* Contract Swithces */

