/* Hide the browser's default checkbox */
.oneCoinContainer input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 8px;
    left: 0;
    height: 22px;
    width: 22px;
    background-color: transparent;
    border: 1px solid #1dccff;
}

/* On mouse-over, add a grey background color */
.oneCoinContainer:hover input~.checkmark {
    background-color: transparent;
}

/* When the checkbox is checked, add a blue background */
.oneCoinContainer input:checked~.checkmark {
    background-color: transparent;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.oneCoinContainer input:checked~.checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.oneCoinContainer .checkmark:after {
    left: 6px;
    top: 1px;
    width: 8px;
    height: 15px;
    border: solid #14c048;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
